import { Component, ViewChild } from '@angular/core';

import {
  PDFSource,
} from 'ng2-pdf-viewer';

declare var jQuery: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  pdfSrc: string | PDFSource  = '../assets/pdftron-sdk.pdf';

  error: any;
  page = 1;
  rotation = 0;
  zoom = 1.0;
  originalSize = true;
  pdf: any;
  renderText = true;
  stickToPage = false;
  showAll = true;
  autoresize = true;
  fitToPage = true;
  isOutlineShown = false;
  pdfQuery = '';
  pinchZoomEnabled = false;
  startX = 0;
  startY = 0;
  initialPinchDistance = 0;
  pinchScale = 1;

  openLocalFile() {
    jQuery('#file').trigger('click');
  }

  toggleOutline() {
    this.isOutlineShown = !this.isOutlineShown;
  }

  incrementPage(amount: number) {
    this.page += amount;
  }

  incrementZoom(amount: number) {
    this.zoom += amount;
  }

  rotate(angle: number) {
    this.rotation += angle;
  }

  /**
   * Render PDF preview on selecting file
   */
  onFileSelected() {
    const $pdf: any = document.querySelector('#file');

    if (typeof FileReader !== 'undefined') {
      const reader = new FileReader();

      reader.onload = (e: any) => {
        this.pdfSrc = e.target.result;
      };

      reader.readAsArrayBuffer($pdf.files[0]);
    }
  }

  /**
   * Get pdf information after it's loaded
   * @param pdf
   */

  /**
   * Handle error callback
   *
   * @param error
   */
  onError(error: any) {
    this.error = error; // set error

    if (error.name === 'PasswordException') {
      const password = prompt(
        'This document is password protected. Enter the password:'
      );

      if (password) {
        this.error = null;
        this.setPassword(password);
      }
    }
  }

  setPassword(password: string) {
    let newSrc;
    if (this.pdfSrc instanceof ArrayBuffer) {
      newSrc = { data: this.pdfSrc };
    } else if (typeof this.pdfSrc === 'string') {
      newSrc = { url: this.pdfSrc };
    } else {
      newSrc = { ...this.pdfSrc };
    }
    newSrc.password = password;
    this.pdfSrc = newSrc;
  }

  getInt(value: number): number {
    return Math.round(value);
  }

  /**
   * Navigate to destination
   * @param destination
   */

  /**
   * Page rendered callback, which is called when a page is rendered (called multiple times)
   *
   * @param {CustomEvent} e
   */
  pageRendered() {
    const page = document.querySelector('.page');
    console.log(page)
    page?.insertAdjacentHTML('afterend', '<div id="video-container"><div id="mini-video"><iframe width="100%" height="100%" style="position: absolute; left: 0; top: 0;" src="https://www.youtube.com/embed/OwQaBbW3OKU?playsinline=1" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe></div></div>');
    const viewerContainer = document.getElementById('viewerContainer');
    const video = document.getElementById('video-container');
    viewerContainer?.addEventListener('scroll', () => {
      if (!this.isVideoInViewport(video)) {
        video?.firstElementChild?.classList.add('fixed');

      } else if (this.isVideoInViewport(video)) {
        video?.firstElementChild?.classList.remove('fixed');
      }
    });
    if (!this.pinchZoomEnabled) {
      this.pinchZoomEnabled = true;
      this.enablePinchZoom();
    }
  }

  private isVideoInViewport(el: any): any {
    const video = el.getBoundingClientRect();
    const windowHeight = (window.innerHeight || document.documentElement.clientHeight);
    const windowWidth = (window.innerWidth || document.documentElement.clientWidth);
    const vertInView = (video.top <= windowHeight) && ((video.top + video.height) >= 0);
    const horInView = (video.left <= windowWidth) && ((video.left + video.width) >= 0);
    return (vertInView && horInView);
  }

  enablePinchZoom(): void {
    const viewer = document.getElementById('viewer') as HTMLDivElement;
    const container = document.getElementById('viewerContainer') as HTMLDivElement;
    const reset = () => { this.startX = this.startY = this.initialPinchDistance = 0; this.pinchScale = 1; };
    // Prevent native iOS page zoom
    // document.addEventListener("touchmove", (e) => { if (e.scale !== 1) { e.preventDefault(); } }, { passive: false });
    document.addEventListener('touchstart', (e) => {
      if (e.touches.length > 1) {
        this.startX = (e.touches[0].pageX + e.touches[1].pageX) / 2;
        this.startY = (e.touches[0].pageY + e.touches[1].pageY) / 2;
        this.initialPinchDistance = Math.hypot((e.touches[1].pageX - e.touches[0].pageX), (e.touches[1].pageY - e.touches[0].pageY));
      } else {
        this.initialPinchDistance = 0;
      }
    });
    document.addEventListener('touchmove', (e) => {
      if (this.initialPinchDistance <= 0 || e.touches.length < 2) { return; }
      if ( this.pinchScale !== 1) { e.preventDefault(); }
      const pinchDistance = Math.hypot((e.touches[1].pageX - e.touches[0].pageX), (e.touches[1].pageY - e.touches[0].pageY));
      const originX = this.startX + container.scrollLeft;
      const originY = this.startY + container.scrollTop;
      this.pinchScale = pinchDistance / this.initialPinchDistance;
      viewer.style.transform = `scale(${this.pinchScale})`;
      viewer.style.transformOrigin = `${originX}px ${originY}px`;
    }, { passive: false });
    document.addEventListener('touchend', (e) => {
      if (this.initialPinchDistance <= 0) { return; }
      viewer.style.transform = `none`;
      viewer.style.transformOrigin = `unset`;
      const PDFViewerApplication: any = (window as any).PDFViewerApplication;
      PDFViewerApplication.pdfViewer.currentScale *= this.pinchScale;
      const rect = container.getBoundingClientRect();
      const dx = this.startX - rect.left;
      const dy = this.startY - rect.top;
      container.scrollLeft += dx * (this.pinchScale - 1);
      container.scrollTop += dy * (this.pinchScale - 1);
      reset();
    });
  }



}
